#projects {
  text-align: center;
  background: #92A8A6;
  padding-bottom: 40px;
  width: 100%;
}

#projects h2 {
  color: #494949;
  text-align: center;
  font-weight: 700;
  font-size: 1.8em;
  margin: auto;
  padding: 25px 0px;
  width: 100%;
}

.projects-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;;
  margin: auto;
  max-width: 1200px;
}

.individual-project {
  position: relative;
  border: 2px solid #18261d;
  overflow: hidden;
  margin: 10px;
  max-width: 47%;
  min-width: 400px;
}

.individual-project .project-image {
  display: block;
  width: 100%;
  height: 100%;
}

.article-layer {
  background: rgb(206, 206, 206);;
  -webkit-transition: opacity 1000ms;
  -o-transition: opacity 1000ms;
  transition: opacity 1000ms;
  opacity: 0;
  position: absolute;
  top: 0;
  text-align: center;
  width: 100%;
  height: 100%;
}

.article-layer:hover {
  opacity: .9;
}

.article-layer h3 {
  padding: 25px 0 15px 0;
  margin-bottom: 20px;
  text-align: center;
  font-size: 2em;
  font-weight: 700;
}

.article-layer p {
  margin: 0 auto;
  text-align: left;
  width: 90%;
}

.article-layer .project-icons {
  padding: 10px;
  width: 60px;
}

.article-layer .project-btn {
  position: relative;
  top: 30px;
  height: 35px;
  width: 100px;
  border: none;
  border-radius: 5px;
  background: #000;
  color: #FFF;
  cursor: pointer;
  outline: none;
}

.project-btn:hover {
  color: #CC8B86;
}


