#footer {
  background-color: #CC8B86;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100px;
}

.footer-icons {
  width: 80px;
  height: 80px;
}

.github {
  background: transparent url('../../styles/images/github-logo.svg') no-repeat 8px 8px / 30px;
}

.linkedin {
  background: transparent url('../../styles/images/linkedin-button.svg') no-repeat 8px 8px / 30px;
}

.twitter {
  background: transparent url('../../styles/images/twitter.svg') no-repeat 8px 8px / 30px;
}

.contact {
  background: transparent url('../../styles/images/contact.svg') no-repeat 8px 8px / 30px;
}

#scroll-up {
  position: absolute;
  right: 25px;
  width: 35px;
  height: 35px;
  margin: inherit;
  border: none;
  cursor: pointer;
  background: url('../../styles/images/arrow.svg')
}

.footer-tag {
  position: absolute;
  bottom: 25px;
  left: 20px;
  font-size: 0.8em;
}



